@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap');

.main-page {
  display: flex;
  width: 100vw;
  height: 78vh;
  overflow: hidden;
  background-image: url("../img/general/main-background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Open Sans', sans-serif;

  &__animal-img {
    width: 209px;
    height: 223px;
    position: absolute;
    left: 5%;
    bottom: 17%;
    background-image: url("../img/general/animal.png");
  }

  &__description-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: 10%;
    left: 20%;
  }
}

.description {
  display: flex;
  flex-direction: column;
  width: 550px;
  padding: 25px 30px;
  border: 3px solid transparent;
  box-sizing: border-box;
  position: relative;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 2;

  &__border-figure {
    width: 50px;
    height: 50px;
    position: absolute;
    background-image: url("../img/general/description-border-figure.png");

    &--left-top {
      top: 0;
      left: 0;
      transform: rotate(90deg);
    }

    &--right-top {
      top: 0;
      right: 0;
      transform: rotate(180deg);
    }

    &--right-bottom {
      bottom: 0;
      right: 0;
      transform: rotate(-90deg);
    }

    &--left-bottom {
      bottom: 0;
      left: 0;
    }
  }

  &__title {
    line-height: 50px;
    text-align: center;
    font-size: 46px;
    font-weight: bold;
    color: $red;

    &:after {
      content: '';
      display: flex;
      width: 100%;
      height: 1px;
      margin: 12px 0;
      background-color: $grey;
    }
  }

  &__main-text {
    line-height: 36px;
    text-transform: uppercase;
    text-align: center;
    font-size: 28px;
    font-weight: bold;
    color: $white;

    &--yellow-bigger-text {
      color: $yellow;
      font-size: 34px;
    }

    &:after {
      content: '';
      display: flex;
      width: 100%;
      height: 1px;
      margin: 12px 0;
      background-color: $grey;
    }
  }

  &__text {
    line-height: 16px;
    text-align: center;
    font-size: 12px;
    color: $white;

    &--yellow-text {
      color: $yellow;
    }
  }
}

.sign-up {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 450px;
  margin-top: 15%;
  position: relative;
  z-index: 1;

  &__animation {
    width: 127px;
    height: 144px;
    position: absolute;
    background-image: url("../img/general/button-animation.gif");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    &--bottom {
      top: 50px;
      transform: rotate(90deg) scale(1, -1);
    }

    &--right {
      right: 0;
      transform: rotate(0deg) scale(1, -1);
    }

    &--top {
      bottom: 50px;
      transform: rotate(270deg) scale(1, -1);
    }

    &--left {
      left: 0;
      transform: rotate(180deg) scale(1, -1);
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 225px;
    height: 75px;
    border-radius: 4px;
    position: relative;
    background: linear-gradient(180deg, #E03D22 0%, #B12D17 100%);
    box-shadow: 1px 1px 0 #000000;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 30px;
    color: $white;
  }
}

.image-layout {
  display: flex;
  position: absolute;
  top: 100px;
  right: 45%;

  &__parallax-img {
    width: 470px;
    height: 79vh;
    position: absolute;
    background-image: url("../img/general/cards_chips.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }

  &__fixed-img {
    width: 470px;
    height: 79vh;
    position: absolute;
    top: 50px;
    background-image: url("../img/general/girl.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

@media (max-width: 1470px) {
  .main-page {
    &__animal-img {
      display: none;
    }

    &__description-wrap {
      left: 15%;
    }
  }
  .image-layout {
    right: 47%;

    &__fixed-img {
      top: 30px;
    }
  }
  .sign-up {
    margin-top: 8%;
  }
}

@media (max-width: 991px) {
  .main-page {
    &__description-wrap {
      left: 10%;
    }
  }
  .image-layout {
    right: 75%;
  }
}

@media (max-width: 767px) {
  .main-page {
    &__description-wrap {
      left: 0;
    }
  }
  .description {
    width: 100vw;
  }
  .image-layout {
    width: 100vw;
    right: 0;

    &__fixed-img {
      top: 0;
    }

    &__parallax-img {
      width: 100%;
      left: 0;
    }

    &__fixed-img {
      width: 100%;
      left: 0;
    }
  }
}

@media (max-width: 579px) {
  .description {
    padding: 10px;

    &__border-figure {
      background-image: url("../img/general/mob/mob-description-border-figure.png");
    }

    &__title {
      line-height: 28px;
      font-size: 26px;
    }

    &__main-text {
      line-height: 22px;
      font-size: 18px;

      &--yellow-bigger-text {
        font-size: 24px;
      }
    }

    &__text {
      line-height: 14px;
      font-size: 10px;
    }
  }

  .sign-up {
    max-width: 100vw;
    bottom: 0;
    margin-top: 3%;

    &__animation {
      background-image: url("../img/general/button-animation-static.png");
    }

    &__button {
      width: 146px;
      height: 46px;
      font-size: 20px;
    }
  }
  .image-layout {
    top: 0;

    &__parallax-img {
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    &__fixed-img {
      top: 35px;
      background-position: 35% -25%;
      background-size: 115%;
    }
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .sign-up {

    &__animation {
      &--bottom {
        top: 50px;
        right: 160px;
        transform: rotate(90deg) scale(1, -1);
      }

      &--right {
        right: 0;
        bottom: -20px;
        transform: rotate(0deg) scale(1, -1);
      }

      &--top {
        bottom: 50px;
        left: 150px;
        transform: rotate(270deg) scale(1, -1);
      }

      &--left {
        left: 0;
        bottom: -32px;
        transform: rotate(180deg) scale(1, -1);
      }
    }
  }
}
