@font-face {
  font-family: BauhausMedium;
  src: url('../fonts/BauhausMediumBT.ttf');
}

@font-face {
  font-family: BauhausBold;
  src: url('../fonts/BauhausBold.ttf');
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 6vh;
  box-sizing: border-box;
  padding: 0 10px 0 20px;
  position: relative;
  background: $dark;
  z-index: 3;
  &__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: relative;
  }
  &__logo {
    display: flex;
    width: 258px;
    height: 39px;
    background-image: url("../img/general/main-logo.png");
  }
}

.jackpot {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  right: 140px;
  font-family: BauhausBold, sans-serif;

  &__count {
    letter-spacing: 3px;
    font-size: 44px;
    color: $white;
  }

  &__sign {
    margin-left: 30px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-size: 42px;
    color: $red;
  }

}
  .page-lang {
    display: flex;
    flex-wrap: wrap;
    max-width: 90px;
    padding: 0 8px 0.5vh 8px;
    position: absolute;
    top: 0.55vh;
    right: 0;
    background-color: $dark;

    &__checked-lang {
      display: flex;
      width: 47px;
      height: 31px;
      border-radius: 3px;
      cursor: pointer;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }

    &__toggle-button {
      width: 30px;
      height: 30px;
      margin-left: 5px;
      background-image: url("../img/general/lang-toggle-button-open.png");
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;

      &--active {
        background-image: url("../img/general/lang-toggle-button-close.png");
      }
    }

    &__list {
      display: flex;
      flex-direction: column;
      height: 0;
      margin-top: 0;
      padding: 0;
      font-family: BauhausBold, sans-serif;
      overflow: hidden;
      transition: all 0.2s ease;

      &--active {
        height: 200px;
        margin-top: 20px;
        transition: all 0.2s ease;
      }
    }

    &__item {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 5px;
      cursor: pointer;
      &:after {
        content: '';
        display: flex;
        width: 100%;
        height: 1px;
        margin-top: 5px;
        background-color: #C4C4C4;
      }
    }

    &__flag {
      width: 47px;
      height: 31px;
      border-radius: 3px;
      background-image: url("../img/general/kz-flag.jpg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }

    &__lang-sign {
      display: flex;
      align-items: center;
      margin-left: 5px;
      text-transform: uppercase;
      font-size: 20px;
      color: $white;
    }

    &__toggle-list-button {

    }
  }

@media (max-width: 991px) {
  .jackpot {
    top: 115%;
    right: 110px;
  }
}
@media (max-width: 579px) {
  .header {
    &__logo {
      display: flex;
      width: 139px;
      height: 21px;
      background-image: url("../img/general/mob/mob-main-logo.png");
    }
  }
  .jackpot {
    justify-content: center;
    width: 100%;
    top: 6vh;
    right: 0;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.9);
    &__count {
      letter-spacing: 1px;
      font-size: 28px;
    }
    &__sign {
      margin-left: 20px;
      letter-spacing: 0.5px;
      font-size: 28px;
    }
  }
  .page-lang {
    max-width: 60px;
    padding: 0 4px 0.5vh 4px;
    top: 0.25vh;
    &__checked-lang {
      width: 32px;
      height: 21px;
    }

    &__toggle-button {
      width: 20px;
      height: 20px;
    }
    &__list {
      &--active {
        height: 153px;
      }
    }
    &__flag {
      width: 32px;
      height: 21px;
      background-image: url("../img/general/mob/mob-kz-flag.jpg");
    }
    &__lang-sign {
      font-size: 16px;
    }
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .page-lang {
    &__item {
      margin-bottom: 11px;
    }
  }
}

