.footer {
  width: 100vw;
  height: 16vh;
  position: relative;
  background-color: $dark;
  background-image: url("../img/general/footer-background.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  z-index: 2;
}

@media (max-width: 579px) {
  .footer {
    background-image: url("../img/general/mob/mob-footer-background.png");
  }
}